.container-fluid {
  background: #000;
  margin: 0px;

  width: 100%;
}
.row {
  display: flex;
  margin: 0px;
  justify-content: center;
  align-items: center;
}
.col {
}
.App {
  text-align: center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}
/*--------------------------------------
 Hero Sectiom
--------------------------------------*/
#hero .container-fluid {
  background-color: rgb(255, 0, 0);
  width: auto;
  height: fit-content;
  min-width: 100%;
  margin-top: 100px;
  padding: 25px;
  display: grid !important;
}
.table-dark {
  --bs-table-bg: #101d29 !important;
  color: #fff;
  border-color: #373b3e;
  text-align: center;
}
/*--------------------------------------
Main Sectiom
--------------------------------------*/
#main .col-6 {
  background: #92bd81;
  width: 30%;
  height: auto;
}
.mainRow1 {
  display: flex;
}
.mainRow2 {
  max-width: 50%;
  min-width: none;
  width: auto;
  height: fit-content;
}
.container-style {
  display: grid;
  grid-template-columns: 400px 0.5fr;
  grid-column-gap: 5px;
  grid-row-gap: 5px;
  justify-items: stretch;
  align-items: stretch;
}
.nftminter {
  width: 48%;
  background: radial-gradient(
    circle at 50% 50%,
    #090f11,
    #0c1519,
    #101e2b,
    #0f223c,
    #0e2144
  );
  font-family: "SF Pro Display";
  font-weight: 300;
  color: white;
  box-shadow: 0 0 1px 1px #17b2ff;
  border-radius: 20px !important;
  border: 20px !important;
  text-align: center;
  justify-content: center;
  display: flex;
  margin: 20px;
}
