.flex-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  height: initial;
  display: flex;
}
.flex-item {
  box-shadow: 0 0 1px 1px #17b2ff;
  border-radius: 20px !important;
  border: 2px !important;
  background: tomato;
  padding: 9px;
  width: 40%;
  margin-top: 10px;
  height: 100px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  text-align: center;
}
#main .row {
  display: inline-flex;
}
.wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 200px;
}

.btn-group-lg > .btn,
.btn-lg {
  margin-right: 20px;
  box-shadow: none;
  margin-left: 20px;
}

.portalBox {
  background: radial-gradient(
    circle at 50% 50%,
    #090f11,
    #0c1519,
    #101e2b,
    #0f223c,
    #0e2144
  );
  width: 100%;
  height: 80px;
  display: inline-flex;
  padding-left: 10px;
  padding-right: 10px;
  justify-content: center;
}
